    .categories-search {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 30px;
        width: 964px;
        height: 80px;
        padding: 0 40px;
        background: #000000;
        border-radius: 7px;
        @media (max-width: 1580px) {
            width: 850px;
            height: 72px;
        }
        @media (max-width: 1400px) {
            width: 680px;
            height: 65px;
        }
        @media (max-width: 1050px) {
            gap: 10px;
            padding: 0 10px;
            width: 480px;
            height: 58px;
        }
        @media (max-width: 700px) {
            width: 100%;
        }
        @media (max-width: 530px) {
            height: 47px;
            padding: 0 12px;
        }
        svg {
            @media (max-width: 1050px) {
                width: 37px;
                height: 37px;
            }
            @media (max-width: 530px) {
                width: 30px;
                height: 30px;
            }
        }
        &__input {
            width: 100%;
            border-radius: 4px;
            // width: 766px;
            height: 58px;
            display: flex;
            align-items: center;
            background: #FFFFFF;
            font-family: 'Amarante';
            font-style: normal;
            font-weight: 400;
            font-size: 36px;
            line-height: 50px;
            letter-spacing: -0.035em;
            color: #000000;
            padding-left: 35px;
            &:focus {
                outline: 1px solid #CCCCCC;
            }
            &::placeholder {
                font-family: 'Amarante';
                font-style: normal;
                font-weight: 400;
                font-size: 34px;
                line-height: 50px;
                letter-spacing: -0.035em;
                color: #959595;
                @media (max-width: 1050px) {
                    font-size: 27px;
                    line-height: 40px;
                }
                @media (max-width: 530px) {
                    font-size: 21px;
                    line-height: 24px;
                }
            }
            @media (max-width: 1580px) {
                gap: 45px;
                // width: 652px;
                height: 50px;
            }
            @media (max-width: 1400px) {
                gap: 25px;
                // width: 570px;
                height: 50px;
            }
            @media (max-width: 1050px) {
                gap: 10px;
                padding: 0 10px;
                // width: 480px;
                height: 45px;
                font-size: 29px;
                line-height: 35px;
            }
            @media (max-width: 530px) {
                gap: 7px;
                padding: 0 7px;
                // width: 480px;
                height: 36px;
                font-size: 24px;
                line-height: 30px;
            }
        }
    }
    
    .clear-input {
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: transparent;
        border: none;
        // width: 38px;
        // height: 38px;
        // @media (max-width: 1050px) {
        //     width: 32px;
        //     height: 32px;
        // }
        // @media (max-width: 530px) {
        //     width: 28px;
        //     height: 28px;
        // }
        svg {
            @media (max-width: 1050px) {
                width: 28px;
                height: 28px;
            }
            @media (max-width: 530px) {
                width: 25px;
                height: 25px;
            }
        }
    }