@import '../../scss/vars';
.header {
    background-image: url('../../assets/header-bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    // .header__inner
    &__inner {
        display: flex;
        flex-direction: column;
    }
    // .header__top
    &__top {
        display: inline-flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 110px;
        background: rgba(0, 0, 0, 0.59);
        backdrop-filter: blur(3.5px);
        position: relative;
    }
    // .header__content
    &__content {}
}

.logo__link {
    svg {
        width: 260px;
        height: auto;
        @media (max-width: 1065px) {
            width: 220px;
            height: auto;
        }
        @media (max-width: $breakpoint992) {
            width: 200px;
            height: auto;
        }
        @media (max-width: 680px) {
            width: 180px;
            height: auto;
        }
    }
}

.header-nav {
    display: inline-flex;
    justify-content: space-between;
    gap: 70px;
    align-items: center;
    @media (max-width: 780px) {
        gap: 40px;
    }
    @media (max-width: 700px) {
        display: none;
    }
    &__item {}
    // .header-nav__link
    &__link {
        font-family: 'Kenia', cursive;
        font-weight: 400;
        font-size: 40px;
        line-height: 40px;
        /* identical to box height */
        letter-spacing: 0.09em;
        transition: all ease 0.3s;
        color: #FFF4F4;
        &:hover {
            -webkit-text-stroke: 3px #6b6868;
        }
        @media (max-width: $breakpoint992) {
            font-size: 34px;
            line-height: 34px;
        }
        @media (max-width: 680px) {
            font-size: 30px;
            line-height: 30px;
        }
    }
}

.header-cart {
    margin-right: 15px;
    transition: all ease 0.4s;
    &:hover {
        transform: translateY(7px);
    }
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 700px) {
        display: none;
    }
    // .header-cart__link
    &__link {
        width: 73px;
        height: 60px;
        svg {
            @media (max-width: $breakpoint992) {
                width: 65px;
                height: 54px;
            }
            @media (max-width: 680px) {
                width: 60px;
                height: 49px;
            }
        }
    }
    // .header-cart__quantity
    &__quantity {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        right: -15px;
        top: -10px;
        width: 26px;
        height: 26px;
        background: #E2E1E1;
        border-radius: 18px;
        @media (max-width: $breakpoint992) {
            right: -10px;
            top: -6px;
        }
        @media (max-width: 680px) {
            top: -1px;
            right: -5px;
        }
    }
}

.cart-quantity {
    // .cart-quantity__num
    &__num {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 2px;
        /* identical to box height */
        color: #000000;
    }
}

//HEADER-CONTENT
.header-content {
    // .header-content__inner
    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        min-height: 86vh;
        position: relative;
    }
    // .header-content__title
    &__title {
        display: inline;
        text-align: center;
        background-color: #fff;
        font-family: 'Sedgwick Ave Display';
        font-style: normal;
        font-weight: 400;
        font-size: 96px;
        line-height: 120px;
        /* identical to box height */
        letter-spacing: -0.06em;
        color: #750000;
        padding: 5px 0px;
        @media (max-width: 1550px) {
            font-size: 86px;
            line-height: 110px;
        }
        @media (max-width: 1065px) {
            font-size: 78px;
            line-height: 100px;
        }
        @media (max-width: $breakpoint768) {
            font-size: 70px;
            line-height: 80px;
        }
        @media (max-width: 460px) {
            font-size: 60px;
            line-height: 70px;
            margin-bottom: 60px;
        }
    }
    // .header-content__to-collection
}

.to-collection {
    background-color: #fff;
    padding: 15px;
    position: absolute;
    bottom: 20px;
    display: inline-flex;
    gap: 20px;
    justify-content: center;
    align-items: center;
    @media (max-width: $breakpoint768) {
        padding: 6px;
        gap: 12px;
    }
    @media (max-width: 680px) {
        flex-wrap: wrap;
        gap: 5px;
        justify-content: center;
    }
    // .to-collection__text
    &__text {
        font-family: 'Sedgwick Ave Display';
        font-style: normal;
        font-weight: 400;
        font-size: 58px;
        line-height: 80px;
        /* identical to box height */
        letter-spacing: 0.23em;
        @media (max-width: 1550px) {
            font-size: 54px;
            line-height: 70px;
            letter-spacing: 0.15em;
        }
        @media (max-width: 1065px) {
            font-size: 48px;
            line-height: 60px;
            letter-spacing: 0.09em;
        }
        @media (max-width: $breakpoint768) {
            font-size: 42px;
            line-height: 54px;
            letter-spacing: 0.07em;
        }
        @media (max-width: 460px) {
            font-size: 37px;
            line-height: 50px;
            letter-spacing: 0.05em;
        }
        @media (max-width: 330px) {
            text-align: center;
        }
        color: #750000;
    }
    // .to-collection__btn
    &__btn {
        transition: all ease 0.3s;
        justify-content: center;
        align-items: center;
        background-color: transparent;
        border: none;
        &:hover {
            transform: translateY(15px);
        }
        svg {
            @media (max-width: 1550px) {
                height: 75px;
                width: 39px;
            }
            @media (max-width: 1065px) {
                height: 64px;
                width: 33px;
            }
            @media (max-width: $breakpoint768) {
                height: 57px;
                width: 29px;
            }
            @media (max-width: 460px) {
                height: 52px;
                width: 26px;
            }
        }
    }
    // .to-collection__scroll
    &__scroll {}
}

//HEADER-TOP
.header-top {
    position: sticky;
    z-index: 99;
    top: 0px;
    &__inner {
        display: inline-flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 110px;
        @media (max-width: 630px) {
            flex-wrap: wrap;
            gap: 18px;
        }
    }
}

//BURGER
.burger-list {
    padding: 45px 0;
    visibility: hidden;
    opacity: 0;
    position: absolute;
    top: 110px;
    z-index: 5;
    background: rgba(0, 0, 0, 0.81);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 35px;
    transition: all ease-in-out 0.4s;
    &.open {
        opacity: 1;
        visibility: visible;
        .header-cart {
            display: flex;
            margin-right: 0;
        }
        .header-cart__quantity {
            right: -10px;
            top: -6px;
        }
        .header-nav__link {
            font-size: 36px;
            line-height: 36px;
        }
        .header-cart__link {
            width: 65px;
            height: 54px;
        }
    }
}

#burger {
    display: none;
    width: 52px;
    height: 35px;
    position: relative;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: .5s ease-in-out;
    -moz-transition: .5s ease-in-out;
    -o-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
    cursor: pointer;
    span {
        display: block;
        position: absolute;
        height: 7px;
        width: 100%;
        background: #ffffff;
        border-radius: 7px;
        opacity: 1;
        left: 0;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transition: .25s ease-in-out;
        -moz-transition: .25s ease-in-out;
        -o-transition: .25s ease-in-out;
        transition: .25s ease-in-out;
    }
    @media (max-width: 700px) {
        display: block;
    }
}

#burger span:nth-child(1) {
    top: 0px;
    -webkit-transform-origin: left center;
    -moz-transform-origin: left center;
    -o-transform-origin: left center;
    transform-origin: left center;
}

#burger span:nth-child(2) {
    top: 14px;
    -webkit-transform-origin: left center;
    -moz-transform-origin: left center;
    -o-transform-origin: left center;
    transform-origin: left center;
}

#burger span:nth-child(3) {
    top: 28px;
    -webkit-transform-origin: left center;
    -moz-transform-origin: left center;
    -o-transform-origin: left center;
    transform-origin: left center;
}

#burger.open span:nth-child(1) {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
    top: -7px;
    left: 8px;
}

#burger.open span:nth-child(2) {
    width: 0%;
    opacity: 0;
}

#burger.open span:nth-child(3) {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
    top: 30px;
    left: 8px;
}