.sort-select {
    position: relative;
    display: flex;
    @media (max-width: 900px) {
        margin-left: auto;
    }
    // .sort-select__header
    &__header {
        display: inline-flex;
        align-items: center;
        justify-content: space-around;
        width: 444px;
        height: 80px;
        background: #020202;
        border-radius: 6px;
        @media (max-width: 1580px) {
            width: 400px;
            height: 72px;
        }
        @media (max-width: 1400px) {
            height: 60px;
        }
        @media (max-width: 1050px) {
            width: 330px;
            height: 58px;
        }
        @media (max-width: 940px) {
            width: 270px;
            height: 50px;
        }
        svg {
            @media (max-width: 1050px) {
                width: 36px;
                height: 21px;
            }
            @media (max-width: 940px) {
                width: 28px;
                height: 16px;
            }
        }
    }
    // .sort-select__current
    &__current {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 36px;
        line-height: 45px;
        /* identical to box height */
        letter-spacing: -0.035em;
        color: #c2c2c2;
        @media (max-width: 1580px) {
            font-size: 32px;
            line-height: 40px;
        }
        @media (max-width: 1050px) {
            font-size: 28px;
            line-height: 30px;
        }
        @media (max-width: 940px) {
            font-size: 24px;
            line-height: 26px;
        }
    }
    // .sort-select__chosen
    &__chosen {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 36px;
        line-height: 45px;
        /* identical to box height */
        letter-spacing: -0.035em;
        color: #FFFFFF;
        @media (max-width: 1580px) {
            font-size: 32px;
            line-height: 40px;
        }
        @media (max-width: 1050px) {
            font-size: 28px;
            line-height: 30px;
        }
        @media (max-width: 940px) {
            font-size: 24px;
            line-height: 26px;
        }
    }
    // .sort-select__body
    &__body {
        border: 1px solid #fff;
        position: absolute;
        left: 0;
        top: 80px;
        display: none;
        flex-direction: column;
        background: #3B3B3B;
        @media (max-width: 1580px) {
            top: 72px;
        }
        @media (max-width: 1400px) {
            top: 60px;
        }
        @media (max-width: 1050px) {
            top: 57px;
        }
        @media (max-width: 940px) {
            top: 50px;
        }
    }
    // .sort-select__option
    &__option {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid rgb(255, 255, 255);
        padding-left: 30px;
        width: 414px;
        height: 67px;
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 36px;
        line-height: 45px;
        /* identical to box height */
        letter-spacing: -0.035em;
        background: #989898;
        color: #FFFFFF;
        transition: all ease 0.3s;
        @media (max-width: 1580px) {
            width: 370px;
            height: 62px;
            font-size: 32px;
            line-height: 40px;
        }
        @media (max-width: 1050px) {
            font-size: 29px;
            line-height: 35px;
            width: 300px;
            height: 50px;
        }
        @media (max-width: 940px) {
            font-size: 25px;
            line-height: 25px;
            width: 240px;
            height: 40px;
        }
        &:hover {
            background: #3b3b3b;
        }
    }
}

.sort-select__body.active-select {
    display: flex;
}

.sort-select__option.selected-option {
    background: #777777;
}