@import '../../scss/vars';
.item {
    background-image: url('../../assets/header-bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 60px 0;
    min-height: 76vh;
    @media (max-width: 1200px) {
        padding: 40px 0;
    }
    @media (max-width: 500px) {
        padding-top: 25px;
    } // .item__inner
    &__inner {
        padding-top: 60px;
        min-height: 76vh;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        background: rgba(255, 255, 255, 0.81);
        @media (max-width: 940px) {
            padding-top: 30px;
        }
    }
    // .item__content
    &__content {
        gap: 60px;
        display: inline-flex;
        align-items: center;
        justify-content: space-around;
        padding-top: 60px;
        @media (max-width: 940px) {
            padding-top: 60px;
            gap: 35px;
        }
        @media (max-width: 780px) {
            flex-wrap: wrap;
            gap: 45px;
        }
        @media (max-width: 500px) {
            gap: 35px;
            justify-content: center;
        }
    }
    // .item__slider
    &__slider {}
}

.item-slider {
    margin-bottom: 60px;
    margin-left: 60px;
    display: inline-flex;
    align-items: center;
    column-gap: 35px;
    // .item-slider__wrapper
    &__wrapper {
        margin-top: 0;
        width: 100%;
        overflow: hidden;
    }
    @media (max-width: $breakpoint1450) {
        margin-left: 50px;
        column-gap: 25px;
    }
    @media (max-width: 1200px) {
        margin-left: 40px;
        column-gap: 20px;
    }
    @media (max-width: 940px) {
        margin-left: 25px;
        column-gap: 10px;
        margin-bottom: 0;
    }
    @media (max-width: 780px) {
        margin-bottom: 0px;
        margin-left: 0px;
        column-gap: 22px;
    }
    @media (max-width: 500px) {
        column-gap: 4px;
    }
    &__prev {
        svg {
            @media (max-width: 780px) {
                height: 80px;
                width: 70px;
            }
            @media (max-width: 580px) {
                width: 58px;
                height: 66px;
            }
            @media (max-width: 500px) {
                width: 50px;
                height: 57px;
            }
            @media (max-width: 370px) {
                width: 40px;
                height: 47px;
            }
        }
    }
    &__next {
        svg {
            @media (max-width: 780px) {
                height: 80px;
                width: 70px;
            }
            @media (max-width: 580px) {
                width: 58px;
                height: 66px;
            }
            @media (max-width: 500px) {
                width: 50px;
                height: 57px;
            }
            @media (max-width: 370px) {
                width: 40px;
                height: 47px;
            }
        }
    }
}

//ITEM SLIDER
.item-slider__inner {
    display: inline-flex;
    align-items: center;
    transition: 0.5s all ease;
}

.item-slider__slide {
    transition: 1s;
    width: 325px;
    height: 328px;
    @media (max-width: 1200px) {
        width: 290px;
        height: 293px;
    }
    @media (max-width: 940px) {
        width: 260px;
        height: 263px;
    }
    @media (max-width: 780px) {
        width: 325px;
        height: 328px;
    }
    @media (max-width: 580px) {
        width: 260px;
        height: 263px;
    }
    @media (max-width: 500px) {
        width: 240px;
        height: 243px;
    }
    @media (max-width: 420px) {
        width: 200px;
        height: 203px;
    }
    @media (max-width: 370px) {
        width: 185px;
        height: 188px;
    }
}

.item-slider__img {
    width: 325px;
    height: 328px;
    @media (max-width: 1200px) {
        width: 290px;
        height: 293px;
    }
    @media (max-width: 940px) {
        width: 260px;
        height: 263px;
    }
    @media (max-width: 780px) {
        width: 325px;
        height: 328px;
    }
    @media (max-width: 580px) {
        width: 260px;
        height: 263px;
    }
    @media (max-width: 500px) {
        width: 240px;
        height: 243px;
    }
    @media (max-width: 420px) {
        width: 200px;
        height: 203px;
    }
    @media (max-width: 370px) {
        width: 185px;
        height: 188px;
    }
}

// ITEM CONTENT
.item-info {
    flex: 0 1 50%;
    height: 385px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    @media (max-width: 1200px) {
        height: 335px;
    }
    @media (max-width: 940px) {
        height: 290px;
    }
    @media (max-width: 780px) {
        height: auto;
        flex: 0 1 100%;
        justify-content: center;
        align-items: center;
        gap: 28px;
    }
}

.item-naming {
    font-family: 'Kenia';
    font-style: normal;
    font-weight: 400;
    font-size: 64px;
    line-height: 64px;
    text-align: center;
    color: #000000;
    @media (max-width: $breakpoint1450) {
        font-size: 56px;
        line-height: 56px;
    }
    @media (max-width: 1200px) {
        font-size: 50px;
        line-height: 50px;
    }
    @media (max-width: 940px) {
        font-size: 43px;
        line-height: 45px;
    }
    @media (max-width: 780px) {
        font-size: 50px;
        line-height: 50px;
    }
    @media (max-width: 580px) {
        font-size: 43px;
        line-height: 43px;
    }
    @media (max-width: 460px) {
        font-size: 34px;
        line-height: 34px;
    }
    @media (max-width: 380px) {
        font-size: 30px;
        line-height: 30px;
    }
}

.item-price {
    display: inline-flex;
    justify-content: center;
    margin: 0 auto;
    font-family: 'Amarante';
    font-style: normal;
    font-weight: 400;
    font-size: 52px;
    line-height: 65px;
    /* identical to box height */
    color: #000000;
    span {
        margin-left: 6px;
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 52px;
        line-height: 65px;
        /* identical to box height */
        color: #fff;
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: #000000;
        @media (max-width: $breakpoint1450) {
            font-size: 47px;
            line-height: 50px;
            -webkit-text-stroke-width: 1.7px;
        }
        @media (max-width: 1200px) {
            font-size: 42px;
            line-height: 45px;
        }
        @media (max-width: 940px) {
            font-size: 37px;
            line-height: 40px;
        }
        @media (max-width: 780px) {
            font-size: 42px;
            line-height: 45px;
        }
        @media (max-width: 580px) {
            font-size: 37px;
            line-height: 37px;
        }
        @media (max-width: 460px) {
            font-size: 30px;
            line-height: 33px;
        }
        @media (max-width: 380px) {
            font-size: 27px;
            line-height: 30px;
        }
    }
    @media (max-width: $breakpoint1450) {
        font-size: 47px;
        line-height: 50px;
    }
    @media (max-width: 1200px) {
        font-size: 42px;
        line-height: 47px;
    }
    @media (max-width: 940px) {
        font-size: 37px;
        line-height: 40px;
    }
    @media (max-width: 780px) {
        font-size: 42px;
        line-height: 45px;
    }
    @media (max-width: 580px) {
        font-size: 37px;
        line-height: 37px;
    }
    @media (max-width: 460px) {
        font-size: 30px;
        line-height: 33px;
    }
    @media (max-width: 380px) {
        font-size: 27px;
        line-height: 30px;
    }
}

.add-to-cart {
    background: #000000;
    border-radius: 30px;
    padding: 14px 20px;
    transition: all ease 0.3s;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
    border: none;
    font-family: 'Amarante';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 40px;
    /* identical to box height */
    letter-spacing: -0.015em;
    color: #ffffff;
    &:hover {
        box-shadow: 10px 10px 4px rgba(0, 0, 0, 0.39);
    }
    @media (max-width: $breakpoint1450) {
        padding: 10px 14px;
        font-size: 28px;
        line-height: 37px;
        gap: 12px;
        svg {
            width: 43px;
            height: auto;
        }
    }
    @media (max-width: 940px) {
        padding: 10px 11px;
        font-size: 25px;
        gap: 12px;
        svg {
            width: 32px;
            height: auto;
        }
    }
    @media (max-width: 780px) {
        padding: 10px 14px;
        font-size: 28px;
        line-height: 37px;
        gap: 12px;
        svg {
            width: 43px;
            height: auto;
        }
    }
    @media (max-width: 580px) {
        padding: 10px 11px;
        font-size: 25px;
        gap: 12px;
        svg {
            width: 32px;
            height: auto;
        }
    }
    @media (max-width: 460px) {
        padding: 5px 10px;
        font-size: 22px;
        gap: 10px;
        svg {
            width: 28px;
            height: auto;
        }
    }
}

.item-btns {
    margin-top: auto;
    padding-bottom: 45px;
    padding-left: 45px;
    padding-right: 45px;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 940px) {
        padding-bottom: 25px;
        padding-left: 25px;
        padding-right: 25px;
        margin-top: 60px;
    }
    @media (max-width: 580px) {
        padding-bottom: 25px;
        padding-left: 15px;
        padding-right: 15px;
        margin-top: 40px;
        gap: 13px;
    }
    @media (max-width: 460px) {
        flex-wrap: wrap;
    }
}

.go-back-btn {
    background: rgba(255, 255, 255, 0.46);
    border-radius: 30px;
    padding: 12px 40px;
    display: inline-flex;
    gap: 18px;
    transition: all ease 0.3s;
    justify-content: center;
    align-items: center;
    font-family: 'Amarante';
    font-style: normal;
    font-weight: 400;
    font-size: 26px;
    line-height: 30px;
    /* identical to box height */
    text-align: center;
    letter-spacing: 0.06em;
    color: #4E4949;
    &:hover {
        box-shadow: 0px 0px 4px 5px rgba(0, 0, 0, 0.32);
    }
    border: none;
    @media (max-width: 1200px) {
        padding: 8px 34px;
    }
    @media (max-width: 580px) {
        padding: 6px 25px;
        font-size: 22px;
        line-height: 26px;
        svg {
            width: 10px;
            height: 20px;
        }
    }
    @media (max-width: 500px) {
        padding: 7px 29px;
        font-size: 23px;
        line-height: 26px;
        svg {
            width: 13px;
            height: 23px;
        }
    }
}

.go-to-payment-btn {
    transition: all ease 0.3s;
    padding: 17px 24px;
    background: #000000;
    border-radius: 30px;
    &:hover {
        box-shadow: 20px 12px 4px rgba(0, 0, 0, 0.39);
    }
    font-family: 'Amarante';
    font-style: normal;
    font-weight: 400;
    font-size: 27px;
    border: none;
    /* identical to box height */
    letter-spacing: -0.015em;
    color: #ffffff;
    display: inline-flex;
    gap: 18px;
    justify-content: center;
    align-items: center;
    @media (max-width: 1200px) {
        padding: 12px 20px;
    }
    @media (max-width: 580px) {
        padding: 10px 15px;
        font-size: 24px;
        svg {
            width: 11px;
            height: 21px;
        }
    }
    @media (max-width: 460px) {
        margin-left: auto;
    }
}