@import url(../../pages/ItemPage/itemPage.scss);
.cart-popup {
    width: 100%;
    background: rgba(9, 9, 9, 0.91);
    position: absolute;
    top: 110px;
    z-index: 99px;
}

.cart-popup__close {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    background-color: transparent;
    border: none;
    position: absolute;
    right: 5px;
    top: 15px;
}

.cart-popup__quantity {
    -webkit-text-stroke-color: white;
    -webkit-text-stroke-width: 0px;
}

.cart-popup__title {
    text-align: center;
    color: #fff;
    margin: 20px 0;
}

.go-to-cart {
    background-color: #fff;
    color: #000;
}

.cart-popup__item-price {
    color: #fff;
}

.cart-popup__item-naming {
    color: #ffff;
}

.cart-popup__item-size {
    color: #fff;
}

.cart-popup__total {
    color: #fff;
    span {
        margin-left: 8px;
        color: #fff;
        -webkit-text-stroke-color: white;
        -webkit-text-stroke-width: 0px;
    }
}

.cart-popup__inner {
    padding: 10px 0;
    position: relative;
}