@import '../../scss/vars.scss';
.main {
    display: flex;
    flex-direction: column;
}

//TRENDS
.trends {
    background: conic-gradient(from 180deg at 50% 50%, #000000 0deg, #000000 34.14deg, #404040 54.59deg, #000000 72.18deg, #000000 136.74deg, #000000 227.55deg, #000000 288.25deg, #5E5E5E 304.9deg, #000000 329.01deg, #000000 360deg);
    // .trends__inner
    &__inner {
        display: flex;
        flex-direction: column;
    }
    // .trends__title
    &__title {
        margin: 10px 0;
        font-family: 'Kenia';
        font-style: normal;
        font-weight: 400;
        font-size: 128px;
        line-height: 128px;
        /* identical to box height */
        color: #FFFFFF;
        span {
            margin-left: 40px;
            @media (max-width: 1550px) {
                margin-left: 35px;
            }
            @media (max-width: 1050px) {
                margin-left: 25px;
            }
            @media (max-width: 500px) {
                margin-left: 15px;
            }
        }
        @media (max-width: 1550px) {
            font-size: 116px;
            line-height: 115px;
        }
        @media (max-width: 1050px) {
            font-size: 100px;
            line-height: 100px;
        }
        @media (max-width: 900px) {
            font-size: 94px;
            line-height: 94px;
            margin-bottom: 30px;
        }
        @media (max-width: 650px) {
            font-size: 84px;
            line-height: 84px;
        }
        @media (max-width: 500px) {
            font-size: 65px;
            line-height: 60px;
            margin-top: 10px;
            margin-bottom: 20px;
        }
        @media (max-width: 450px) {
            font-size: 58px;
            line-height: 60px;
        }
        @media (max-width: 390px) {
            font-size: 50px;
            line-height: 50px;
        }
    }
}

//HOME CATEGORIES
.home-categories {
    background: #FFFFFF;
    // .home-categories__inner
    &__inner {
        display: flex;
        flex-direction: column;
    }
    // .home-categories__title
    &__title {
        margin: 10px 0;
        text-align: center;
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 128px;
        line-height: 160px;
        /* identical to box height */
        color: #FFFFFF;
        -webkit-text-stroke: 6px #000;
        @media (max-width: 1550px) {
            font-size: 116px;
            line-height: 145px;
        }
        @media (max-width: 1450px) {
            font-size: 104px;
            line-height: 130px;
        }
        @media (max-width: 1050px) {
            font-size: 92px;
            line-height: 110px;
        }
        @media (max-width: 700px) {
            font-size: 84px;
            line-height: 90px;
            -webkit-text-stroke: 3px #000;
        }
        @media (max-width: 550px) {
            font-size: 76px;
            line-height: 80px;
        }
        @media (max-width: 500px) {
            font-size: 60px;
            line-height: 65px;
        }
        @media (max-width: 390px) {
            font-size: 53px;
            line-height: 55px;
        }
    }
    // .home-categories__cards
    &__cards {
        display: inline-flex;
        flex-wrap: wrap;
        column-gap: 80px;
        row-gap: 65px;
        margin-top: 50px;
        justify-content: center;
        @media (max-width: 1550px) {
            column-gap: 65px;
            row-gap: 45px;
            margin-top: 40px;
        }
        @media (max-width: 500px) {
            row-gap: 30px;
            margin-top: 25px;
        }
        @media (max-width: 390px) {
            row-gap: 20px;
            margin-top: 25px;
        }
    }
    // .home-categories__card
    &__card {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        background: #000000;
        width: 384px;
        height: 488px;
        display: flex;
        flex-direction: column;
        align-items: center;
        transition: all ease 0.3s;
        &:hover {
            box-shadow: 20px 20px 4px rgba(0, 0, 0, 0.41);
        }
        @media (max-width: 1550px) {
            width: 364px;
            height: 463px;
        }
        @media (max-width: 1450px) {
            width: 340px;
            height: 432px;
        }
        @media (max-width: 500px) {
            width: 288px;
            height: 366px;
        }
        @media (max-width: 390px) {
            width: 245px;
            height: 311px;
        }
    }
    // .home-categories__img
    &__img {
        width: 384px;
        height: 389px;
        @media (max-width: 1550px) {
            width: 364px;
            height: 363px;
        }
        @media (max-width: 1450px) {
            width: 340px;
            height: 332px;
        }
        @media (max-width: 500px) {
            width: 288px;
            height: 281px;
        }
        @media (max-width: 390px) {
            width: 245px;
            height: 239px;
        }
    }
    // .home-categories__name
    &__name {
        text-align: center;
        margin-top: 12px;
        font-family: 'Kenia';
        font-style: normal;
        font-weight: 400;
        font-size: 44px;
        line-height: 44px;
        /* identical to box height */
        color: #FFFFFF;
        @media (max-width: 1450px) {
            font-size: 38px;
            line-height: 38px;
        }
        @media (max-width: 500px) {
            font-size: 33px;
            line-height: 35px;
        }
        @media (max-width: 390px) {
            font-size: 29px;
            line-height: 32px;
        }
    }
}

