.not-found {
    background-image: url('../../assets/header-bg.jpg');
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center; // .cart__inner
    &__inner {
        height: 80vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: rgba(73, 73, 73, 0.81);
    }
    // .not-found__oops
    &__oops {
        text-align: center;
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 72px;
        line-height: 90px;
        /* identical to box height */
        color: #000000;
        margin-bottom: 40px;
        margin-top: 0;
        -webkit-text-stroke: 2.5px white;
        @media (max-width: 1350px) {
            font-weight: 400;
            font-size: 65px;
            line-height: 70px;
        }
        @media (max-width: 1050px) {
            font-size: 52px;
            line-height: 52px;
            margin-bottom: 20px;
        }
        @media (max-width: 840px) {
            font-size: 46px;
            line-height: 46px;
            -webkit-text-stroke: 1.3px white;
        }
        @media (max-width: 740px) {
            font-size: 40px;
            line-height: 40px;
        }
        @media (max-width: 540px) {
            font-size: 35px;
            line-height: 35px;
        }
        @media (max-width: 420px) {
            font-size: 30px;
            line-height: 30px;
            -webkit-text-stroke: 1px white;
        }
    }
    // .not-found__make-sure
    &__make-sure {
        text-align: center;
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 64px;
        line-height: 64px;
        /* identical to box height */
        letter-spacing: -0.06em;
        color: #ffffff;
        margin-bottom: 40px;
        margin-top: 0;
        @media (max-width: 1350px) {
            font-size: 58px;
            line-height: 58px;
            margin-bottom: 25px;
        }
        @media (max-width: 1050px) {
            font-size: 46px;
            line-height: 46px;
        }
        @media (max-width: 840px) {
            font-size: 42px;
            line-height: 42px;
        }
        @media (max-width: 740px) {
            font-size: 36px;
            line-height: 36px;
        }
        @media (max-width: 540px) {
            font-size: 32px;
            line-height: 32px;
        }
        @media (max-width: 420px) {
            font-size: 28px;
            line-height: 28px;
        }
    }
    // .not-found__return
    &__return {
        display: inline-flex;
        gap: 16px;
        align-items: center;
        font-family: 'Kenia';
        font-style: normal;
        font-weight: 400;
        font-size: 64px;
        line-height: 64px;
        /* identical to box height */
        text-align: center;
        letter-spacing: 0.01em;
        color: #000000;
        @media (max-width: 1350px) {
            font-size: 58px;
            line-height: 58px;
        }
        @media (max-width: 1050px) {
            font-size: 46px;
            line-height: 46px;
        }
        @media (max-width: 840px) {
            font-size: 42px;
            line-height: 42px;
        }
        @media (max-width: 740px) {
            font-size: 36px;
            line-height: 36px;
            gap: 7px;
        }
        @media (max-width: 540px) {
            font-size: 32px;
            line-height: 32px;
        }
    }
}

.not-found-return {
    @media (max-width: 440px) {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }
    // .not-found-return__link
    &__link {
        padding: 0 10px;
        font-family: 'Kenia';
        font-style: normal;
        font-weight: 400;
        font-size: 64px;
        line-height: 64px;
        /* identical to box height */
        text-align: center;
        letter-spacing: 0.01em;
        color: #fff;
        text-decoration-line: underline;
        display: inline-flex;
        align-items: center;
        gap: 25px;
        @media (max-width: 1350px) {
            font-weight: 400;
            font-size: 58px;
            line-height: 58px;
        }
        @media (max-width: 1050px) {
            font-size: 46px;
            line-height: 46px;
        }
        @media (max-width: 840px) {
            font-size: 42px;
            line-height: 42px;
        }
        @media (max-width: 740px) {
            font-size: 36px;
            line-height: 36px;
            gap: 10px;
        }
        @media (max-width: 540px) {
            font-size: 32px;
            line-height: 32px;
        }
        @media (max-width: 440px) {
            font-size: 28px;
            line-height: 28px;
        }
        @media (max-width: 350px) {
            font-size: 26px;
            line-height: 26px;
        }
        svg {
            @media (max-width: 1350px) {
                width: 90px;
            }
            @media (max-width: 1050px) {
                width: 76px;
            }
            @media (max-width: 840px) {
                width: 60px;
            }
            @media (max-width: 740px) {
                width: 50px;
            }
            @media (max-width: 540px) {
                width: 43px;
            }
            @media (max-width: 350px) {
                width: 37px;
            }
        }
    }
}