.slider-wrapper {
    width: 100%;
    overflow: hidden;
}

.slide-wrapper {
    margin-right: 70px;
    @media (max-width: 1245px) {
        margin-right: 55px;
    }
    @media (max-width: 900px) {
        margin-right: 40px;
    }
    @media (max-width: 500px) {
        margin-right: 0px;
    }
    &:last-child {
        margin-right: 0;
    }
}

// SLIDER STYLES
.slider {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 60px;
    align-items: center;
}

.slider-inner {
    transition: 0.5s all ease;
    display: inline-flex;
}

.slider-counter {
    align-self: flex-end;
    display: inline-flex;
    gap: 25px;
    @media (max-width: 1245px) {
        gap: 16px;
    }
    @media (max-width: 900px) {
        gap: 12px;
    }
    @media (max-width: 390px) {
        gap: 7px;
    }
}

.prev-btn {
    display: inline-flex;
    align-items: center;
    svg {
        display: block;
        width: 90;
        height: 47;
        @media (max-width: 1245px) {
            height: 80px;
            width: 70px;
        }
        @media (max-width: 1050px) {
            width: 58px;
            height: 66px;
        }
        @media (max-width: 650px) {
            width: 50px;
            height: 57px;
        }
        @media (max-width: 390px) {
            width: 42px;
            height: 47px;
        }
    }
}

.next-btn {
    display: inline-flex;
    align-items: center;
    svg {
        display: block;
        width: 90;
        height: 47;
        @media (max-width: 1245px) {
            height: 80px;
            width: 70px;
        }
        @media (max-width: 1050px) {
            width: 58px;
            height: 66px;
        }
        @media (max-width: 650px) {
            width: 50px;
            height: 57px;
        }
        @media (max-width: 390px) {
            width: 42px;
            height: 47px;
        }
    }
}

.slider-wrapper {
    margin-top: 60px;
    @media (max-width: 900px) {
        margin-top: 40px;
    }
    @media (max-width: 500px) {
        margin-top: 20px;
    }
}

.slider__count {
    display: inline-flex;
    align-items: center;
    gap: 12px;
    font-family: 'Amarante';
    font-style: normal;
    font-weight: 400;
    font-size: 82px;
    line-height: 102px;
    /* identical to box height */
    color: #000000;
    -webkit-text-stroke: 2.5px white;
    @media (max-width: 1050px) {
        font-size: 56px;
        line-height: 60px;
        -webkit-text-stroke: 2px white;
    }
    @media (max-width: 650px) {
        font-size: 48px;
        line-height: 50px;
        -webkit-text-stroke: 1.5px white;
    }
    @media (max-width: 390px) {
        font-size: 40px;
        line-height: 45px;
    }
}

.slider-current {
    font-family: 'Amarante';
    font-style: normal;
    font-weight: 400;
    font-size: 82px;
    line-height: 102px;
    /* identical to box height */
    color: #FFFFFF;
    @media (max-width: 1550px) {
        font-size: 68px;
        line-height: 80px;
        gap: 7px;
    }
    @media (max-width: 1050px) {
        font-size: 56px;
        line-height: 60px;
    }
    @media (max-width: 650px) {
        font-size: 48px;
        line-height: 50px;
    }
    @media (max-width: 390px) {
        font-size: 40px;
        line-height: 45px;
    }
}

.slider-total {
    font-family: 'Amarante';
    font-style: normal;
    font-weight: 400;
    font-size: 82px;
    line-height: 102px;
    /* identical to box height */
    color: #000000;
    -webkit-text-stroke: 2.5px white;
    @media (max-width: 1550px) {
        font-size: 68px;
        line-height: 80px;
        gap: 7px;
    }
    @media (max-width: 1050px) {
        font-size: 56px;
        line-height: 60px;
    }
    @media (max-width: 650px) {
        font-size: 48px;
        line-height: 50px;
        -webkit-text-stroke: 1.5px white;
    }
    @media (max-width: 390px) {
        font-size: 40px;
        line-height: 45px;
    }
}

.slide {
    width: 345px;
    height: 491px;
    background: #FFFFFF;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    @media (max-width: 1245px) {
        width: 315px;
        height: 448px;
    }
    @media (max-width: 900px) {
        width: 280px;
        height: 399px;
    }
    @media (max-width: 500px) {
        width: 240px;
        height: 342px;
    }
}

.slide-img {
    width: 345px;
    height: 349px;
    margin-bottom: 12px;
    @media (max-width: 1245px) {
        width: 315px;
        height: 318px;
    }
    @media (max-width: 900px) {
        width: 280px;
        height: 296px;
    }
    @media (max-width: 500px) {
        width: 240px;
        height: 254px;
    }
}

.slide-name {
    text-align: center;
    display: flex;
    justify-content: center;
    font-family: 'Sedgwick Ave Display';
    font-style: normal;
    font-weight: 400;
    margin: 12px 0;
    font-size: 28px;
    padding: 0 7px;
    line-height: 32px;
    /* identical to box height */
    color: #000000;
    @media (max-width: 1245px) {
        font-size: 24px;
        line-height: 28px;
    }
    @media (max-width: 900px) {
        font-size: 21px;
        line-height: 25px;
        margin: 5px 0;
    }
}

.slide-price {
    display: flex;
    justify-content: center;
    font-family: 'Sedgwick Ave Display';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 40px;
    /* identical to box height */
    margin-bottom: 12px;
    color: #000000;
    @media (max-width: 1245px) {
        font-size: 28px;
        line-height: 36px;
    }
    @media (max-width: 900px) {
        font-size: 24px;
        line-height: 28px;
        margin-bottom: 5px;
    }
}

