.tabs-skeleton {
    width: 300px;
    height: 79px;
    background: #CECECE;
    border-radius: 7px;
    @media (max-width: 1450px) {
        width: 200px;
        height: 62px;
    }
    @media (max-width: 1050px) {
        width: 190px;
        height: 52px;
    }
    @media (max-width: 900px) {
        width: 180px;
        height: 43px;
    }
    @media (max-width: 700px) {
        width: 150px;
        height: 42px;
    }
    @media (max-width: 530px) {
        width: 130px;
        height: 34px;
    }
    @media (max-width: 400px) {
        width: 110px;
        height: 34px;
    }
}