.cart {
    background-image: url('../../assets/header-bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    padding: 60px 0px 30px 0px; // .cart__inner
    min-height: 78.7vh;
    @media (max-width: 1290px) {
        padding: 40px 0px 15px 0px;
    }
    @media (max-width: 580px) {
        padding: 10px 0px;
    }
    &__inner {
        display: flex;
        min-height: 78.7vh;
        flex-direction: column;
        justify-content: space-between;
        background: rgba(255, 255, 255, 0.81);
    }
    // .cart__top
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 40px 40px 0px 40px;
        @media (max-width: 1290px) {
            padding: 25px 25px 0px 25px;
        }
        @media (max-width: 580px) {
            padding: 15px 15px 0px 15px;
        }
    }
    // .cart__content
    &__content {
        overflow-y: scroll;
    }
    // .cart__bottom
    &__bottom {
        padding: 0px 40px 40px 40px;
        @media (max-width: 1290px) {
            padding: 0px 25px 25px 25px;
        }
        @media (max-width: 580px) {
            padding: 0px 15px 15px 15px;
        }
    }
}

.cart-top {
    margin-top: 0;
    margin-bottom: 25px;
    // .cart-top__title
    &__title {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 48px;
        line-height: 60px;
        letter-spacing: 0.1em;
        color: #000000;
        margin: 0;
        @media (max-width: 1050px) {
            font-size: 44px;
            line-height: 50px;
        }
        @media (max-width: 830px) {
            font-size: 40px;
            line-height: 45px;
        }
        @media (max-width: 580px) {
            font-size: 35px;
            line-height: 40px;
        }
        @media (max-width: 450px) {
            font-size: 30px;
            line-height: 35px;
        }
    } // .cart-top__clear
    &__clear {
        display: inline-flex;
        align-items: center;
        gap: 24px;
        @media (max-width: 1050px) {
            gap: 10px;
        }
        @media (max-width: 830px) {
            gap: 6px;
        }
        svg {
            transition: all ease 0.3s;
            @media (max-width: 1050px) {
                width: 36px;
                height: 42px;
            }
            @media (max-width: 830px) {
                width: 32px;
                height: 37px;
            }
            @media (max-width: 580px) {
                width: 25px;
                height: 30px;
            }
            @media (max-width: 450px) {
                width: 21px;
                height: 26px;
            }
            &:hover {
                filter: drop-shadow(-21px 0px 1px rgba(0, 0, 0, 0.59));
            }
        }
    }
}

.cart-clear {
    // .cart-clear__btn
    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: transparent;
        border: none;
    }
    // .cart-clear__text
    &__text {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 38px;
        line-height: 48px;
        /* identical to box height */
        color: #101010;
        @media (max-width: 1050px) {
            font-size: 32px;
            line-height: 38px;
        }
        @media (max-width: 830px) {
            font-size: 28px;
            line-height: 34px;
        }
        @media (max-width: 580px) {
            font-size: 24px;
            line-height: 28px;
        }
        @media (max-width: 450px) {
            font-size: 20px;
            line-height: 24px;
        }
    }
}

.cart-content {
    display: flex;
    flex-direction: column;
    width: 1070px;
    gap: 30px;
    max-height: 505px;
    margin: 0 auto;
    @media (max-width: 1290px) {
        width: 870px;
        max-height: 375px;
    }
    @media (max-width: 1050px) {
        width: 700px;
        max-height: 325px;
    }
    // @media (max-width: 830px) {
    //     width: 600px;
    //     max-height: 300px;
    // }
    // @media (max-width: 720px) {
    //     width: 500px;
    // }
    @media (max-width: 830px) {
        align-self: center;
        padding-right: 20px;
        width: 94%;
        align-items: center;
    }
    // @media (max-width: 510px) {
    //     width: 370px;
    //     max-height: 270px;
    // }
    // @media (max-width: 450px) {
    //     width: 300px;
    //     max-height: 260px;
    //     margin-top: 30px;
    // }
    // @media (max-width: 380px) {
    //     width: 280px;
    // }
}

.cart-item {
    display: inline-flex;
    align-items: center;
    gap: 25px;
    justify-content: space-between;
    @media (max-width: 1290px) {
        gap: 15px;
    }
    @media (max-width: 1050px) {
        gap: 10px;
    }
    @media (max-width: 830px) {
        flex-wrap: wrap;
        gap: 15px;
        flex: 0 1 100%;
        justify-content: center;
    }
    // @media (max-width: 580px) {
    //     gap: 5px;
    //     flex-wrap: wrap;
    // }
    // .cart-item_img
    &__info {
        width: 100%;
        display: inline-flex;
        justify-content: space-around;
        align-items: center;
    }
    &__buttons {
        width: 100%;
        display: inline-flex;
        justify-content: space-around;
    }
    &_img {
        width: 135px;
        height: 135px;
        @media (max-width: 1290px) {
            width: 120px;
            height: 120px;
        }
        @media (max-width: 1050px) {
            width: 90px;
            height: 90px;
        }
        // @media (max-width: 830px) {
        //     width: 75px;
        //     height: 75px;
        // }
        // @media (max-width: 580px) {
        //     width: 60px;
        //     height: 60px;
        // }
        // @media (max-width: 450px) {
        //     width: 50px;
        //     height: 50px;
        // }
    }
    // .cart-item__text
    &__text {
        // overflow: hidden;
        display: flex;
        flex-direction: column;
        @media (max-width: 830px) {
            // flex: 0 1 65%;
            // max-width: 90px;
            // max-height: 60px;
        }
        // @media (max-width: 450px) {
        //     max-width: 60px;
        //     max-height: 50px;
        // }
    }
    // .cart-item__naming
    &__naming {
        font-family: 'Sedgwick Ave Display';
        font-style: normal;
        font-weight: 400;
        font-size: 38px;
        line-height: 38px;
        /* identical to box height */
        letter-spacing: 0.01em;
        color: #000000;
        margin-bottom: 25px;
        display: inline-flex;
        @media (max-width: 1290px) {
            font-size: 34px;
            line-height: 34px;
            margin-bottom: 15px;
        }
        @media (max-width: 1050px) {
            font-size: 30px;
            line-height: 30px;
            margin-bottom: 10px;
        }
        @media (max-width: 830px) {
            font-size: 28px;
            line-height: 25px;
        }
        @media (max-width: 580px) {
            font-size: 23px;
            line-height: 25px;
        }
        @media (max-width: 450px) {
            font-size: 21px;
            line-height: 23px;
        }
    }
    // .cart-item__size
    &__size {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 30px;
        /* identical to box height */
        letter-spacing: 0.01em;
        color: #606060;
        @media (max-width: 1290px) {
            font-size: 22px;
            line-height: 28px;
        }
        @media (max-width: 830px) {
            font-size: 20px;
            line-height: 21px;
        }
        @media (max-width: 580px) {
            font-size: 17px;
            line-height: 19px;
        }
        @media (max-width: 450px) {
            font-size: 15px;
            line-height: 16px;
        }
    }
    // .cart-item__count
    &__count {}
    // .cart-item__price
    &__price {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 41px;
        line-height: 51px;
        letter-spacing: 0.01em;
        color: #000000;
        @media (max-width: 1290px) {
            font-size: 37px;
            line-height: 45px;
        }
        @media (max-width: 1050px) {
            font-size: 34px;
            line-height: 40px;
        }
        @media (max-width: 830px) {
            font-size: 30px;
            line-height: 34px;
        }
        @media (max-width: 580px) {
            font-size: 25px;
            line-height: 30px;
        }
        // @media (max-width: 450px) {
        //     font-size: 21px;
        //     line-height: 24px;
        // }
    }
    // .cart-item__delete
    &__delete {
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: transparent;
        border: none;
        svg {
            @media (max-width: 1290px) {
                width: 44px;
                height: 44px;
            }
            @media (max-width: 1050px) {
                width: 39px;
                height: 39px;
            }
            @media (max-width: 830px) {
                width: 34px;
                height: 34px;
            }
            @media (max-width: 580px) {
                width: 30px;
                height: 30px;
            }
        }
    }
}

.cart-count {
    width: 215px;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 1290px) {
        width: 165px;
    }
    @media (max-width: 830px) {
        width: 135px;
    }
    @media (max-width: 580px) {
        width: 90px;
    }
    // .cart-count__plus
    &__plus {
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: transparent;
        border: none;
        svg {
            @media (max-width: 1290px) {
                width: 42px;
                height: 42px;
            }
            @media (max-width: 1050px) {
                width: 36px;
                height: 36px;
            }
            @media (max-width: 830px) {
                width: 32px;
                height: 32px;
            }
            @media (max-width: 580px) {
                width: 27px;
                height: 27px;
            }
            // @media (max-width: 450px) {
            //     width: 23px;
            //     height: 23px;
            // }
        }
    }
    // .cart-count__quantity
    &__quantity {
        font-family: 'Kenia';
        font-style: normal;
        font-weight: 400;
        font-size: 54px;
        line-height: 54px;
        /* identical to box height */
        letter-spacing: 0.01em;
        color: #FFFFFF;
        -webkit-text-stroke-color: #000000;
        -webkit-text-stroke-width: 2px;
        @media (max-width: 1290px) {
            font-size: 48px;
            line-height: 48px;
        }
        @media (max-width: 1050px) {
            font-size: 44px;
            line-height: 44px;
        }
        @media (max-width: 830px) {
            font-size: 36px;
            line-height: 36px;
        }
        @media (max-width: 580px) {
            font-size: 26px;
            line-height: 32px;
        }
        // @media (max-width: 450px) {
        //     font-size: 23px;
        //     line-height: 25px;
        // }
    }
    // .cart-count__minus
    &__minus {
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: transparent;
        border: none;
        svg {
            @media (max-width: 1290px) {
                width: 42px;
                height: 42px;
            }
            @media (max-width: 1050px) {
                width: 36px;
                height: 36px;
            }
            @media (max-width: 830px) {
                width: 32px;
                height: 32px;
            }
            @media (max-width: 580px) {
                width: 27px;
                height: 27px;
            }
        }
    }
}

.cart-bottom {
    margin-top: 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 35px;
    @media (max-width: 1050px) {
        gap: 20px;
    }
    @media (max-width: 830px) {
        flex-wrap: wrap;
        justify-content: space-around;
        padding-top: 40px;
        gap: 20px;
    }
    &__total {
        margin-left: auto;
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 40px;
        line-height: 50px;
        /* identical to box height */
        letter-spacing: 0.045em;
        color: #000000;
        @media (max-width: 1290px) {
            font-size: 35px;
            line-height: 40px;
        }
        @media (max-width: 1050px) {
            font-size: 32px;
            line-height: 35px;
        }
        @media (max-width: 580px) {
            font-size: 28px;
            line-height: 32px;
        }
        @media (max-width: 460px) {
            font-size: 24px;
            line-height: 28px;
            margin: 0 auto;
        }
        span {
            font-family: 'Amarante';
            font-style: normal;
            font-weight: 400;
            font-size: 40px;
            line-height: 50px;
            /* identical to box height */
            letter-spacing: 0.045em;
            color: #FFFFFF;
            -webkit-text-stroke-color: #000000;
            -webkit-text-stroke-width: 2px;
            @media (max-width: 1290px) {
                font-size: 38px;
                line-height: 43px;
            }
            @media (max-width: 1050px) {
                font-size: 33px;
                line-height: 36px;
            }
            @media (max-width: 580px) {
                font-size: 29px;
                line-height: 32px;
            }
            @media (max-width: 460px) {
                font-size: 25px;
                line-height: 27px;
                -webkit-text-stroke-width: 1.5px;
            }
        }
    }
}

.cart-bottom__back {
    margin-right: auto;
}