.cart-content_empty {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 95%;
    height: auto;
    align-self: center;
    &__title {
        margin: 0 auto;
        text-align: center;
        display: flex;
        justify-content: center;
        align-self: center;
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 64px;
        line-height: 80px;
        /* identical to box height */
        color: #000000;
        @media (max-width: 1050px) {
            font-size: 58px;
            line-height: 62px;
        }
        @media (max-width: 850px) {
            font-size: 54px;
            line-height: 58px;
        }
        @media (max-width: 740px) {
            font-size: 46px;
            line-height: 50px;
        }
        @media (max-width: 540px) {
            font-size: 40px;
            line-height: 45px;
        }
        @media (max-width: 440px) {
            font-size: 35px;
            line-height: 40px;
        }
        @media (max-width: 370px) {
            font-size: 30px;
            line-height: 35px;
        }
    }
}