.item-slide_skeleton {
    width: 322px;
    height: 328px;
    background: #E8E8E8;
    border-radius: 12px;
    @media (max-width: 1200px) {
        width: 290px;
        height: 293px;
    }
    @media (max-width: 940px) {
        width: 260px;
        height: 263px;
    }
    @media (max-width: 780px) {
        width: 325px;
        height: 328px;
    }
    @media (max-width: 580px) {
        width: 260px;
        height: 263px;
    }
    @media (max-width: 500px) {
        width: 240px;
        height: 243px;
    }
    @media (max-width: 420px) {
        width: 200px;
        height: 203px;
    }
    @media (max-width: 370px) {
        width: 185px;
        height: 188px;
    }
}

.item-naming_skeleton {
    background: #F1EFEF;
    width: 310px;
    height: 64px;
    border-radius: 15px;
    @media (max-width: 940px) {
        width: 275px;
        height: 56px;
    }
    @media (max-width: 830px) {
        width: 250px;
        height: 50px;
    }
    @media (max-width: 460px) {
        width: 210px;
        height: 40px;
    }
}

.item-price_skeleton {
    background: #F1EFEF;
    width: 220px;
    height: 65px;
    border-radius: 15px;
    margin: 0 auto;
    @media (max-width: 940px) {
        width: 180px;
        height: 56px;
    }
    @media (max-width: 830px) {
        width: 165px;
        height: 50px;
    }
    @media (max-width: 460px) {
        width: 125px;
        height: 40px;
    }
}

.add-to-cart_skeleton {
    background: #F1EFEF;
    width: 282px;
    height: 76px;
    border-radius: 30px;
    @media (max-width: 940px) {
        width: 232px;
        height: 60px;
    }
    @media (max-width: 830px) {
        width: 220px;
        height: 55px;
    }
    @media (max-width: 460px) {
        width: 180px;
        height: 40px;
    }
}

.go-to-payment-btn_skeleton {
    background: #F1EFEF;
    width: 250px;
    height: 65px;
    border-radius: 30px;
    @media (max-width: 940px) {
        width: 210px;
        height: 55px;
    }
    @media (max-width: 830px) {
        width: 200px;
        height: 50px;
    }
    @media (max-width: 460px) {
        margin-left: auto;
    }
}