.categories {
    padding: 60px 0;
    background: linear-gradient(180deg, #C9C9C9 0%, #000000 100%);
    @media (max-width: 1450px) {
        padding: 25px 0;
    }
    // .categories__inner
    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__title {
        text-align: center;
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 96px;
        line-height: 120px;
        /* identical to box height */
        color: #000000;
        -webkit-text-stroke: 2.5px white;
        margin: 0 0 55px 0;
        @media (max-width: 1250px) {
            font-size: 90px;
            line-height: 110px;
            margin-bottom: 35px;
        }
        @media (max-width: 1050px) {
            font-size: 70px;
            line-height: 80px;
            margin-bottom: 30px;
        }
        @media (max-width: 700px) {
            font-size: 64px;
            line-height: 70px;
        }
        @media (max-width: 530px) {
            font-size: 54px;
            line-height: 60px;
        }
        @media (max-width: 400px) {
            font-size: 48px;
            line-height: 54px;
            -webkit-text-stroke: 1.5px white;
        }
        @media (max-width: 350px) {
            font-size: 42px;
            line-height: 46px;
            -webkit-text-stroke: 1.5px white;
        }
    }
}

.categories-content__item-link {
    display: block;
}

.categories-content {
    display: inline-flex;
    flex-wrap: wrap;
    gap: 45px;
    justify-content: center;
    @media (max-width: 700px) {
        gap: 15px;
    }
}

//FILTERING
.categories__filtering {
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    margin: 60px 0px;
    gap: 60px;
    @media (max-width: 1580px) {
        gap: 45px;
    }
    @media (max-width: 1450px) {
        flex-wrap: wrap;
        justify-content: center;
        margin: 30px 0px 24px 0px;
        gap: 30px;
    }
    @media (max-width: 700px) {
        flex-wrap: wrap;
        justify-content: center;
        margin: 15px 0px 22px 0px;
        gap: 16px;
    }
}

//CATEGORIES
.header-categories-title {
    text-align: center;
    font-family: 'Amarante';
    font-style: normal;
    font-weight: 400;
    font-size: 110px;
    line-height: 160px;
    margin-top: 40px;
    margin-bottom: 0;
    padding-bottom: 40px;
    /* identical to box height */
    color: #FFFFFF;
    @media (max-width: 1250px) {
        font-size: 96px;
        line-height: 145px;
        margin-top: 25px;
        padding-bottom: 25px;
    }
}