.slider-skeleton {
    width: 345px;
    height: 491px;
    background: #F1EFEF;
    border-radius: 18px;
    display: flex;
    flex-direction: column;
    align-items: center; // .trends-skeleton__img
    margin-right: 70px;
    @media (max-width: 1245px) {
        width: 315px;
        height: 448px;
        margin-right: 55px;
    }
    @media (max-width: 900px) {
        margin-right: 40px;
        width: 280px;
        height: 399px;
    }
    @media (max-width: 500px) {
        width: 240px;
        height: 342px;
        margin-right: 0px;
    }
    &:last-child {
        margin-right: 0;
    }
    &__img {
        margin-top: 5px;
        margin-bottom: 18px;
        width: 345px;
        height: 362px;
        background: #E8E8E8;
        border-radius: 17px;
        @media (max-width: 1245px) {
            width: 315px;
            height: 318px;
        }
        @media (max-width: 900px) {
            width: 280px;
            height: 296px;
        }
        @media (max-width: 500px) {
            width: 240px;
            height: 254px;
        }
    }
    // .trends-skeleton__name
    &__name {
        margin-bottom: 20px;
        width: 345px;
        height: 31px;
        background: #E8E8E8;
        border-radius: 22px;
        @media (max-width: 1245px) {
            width: 315px;
            height: 26px;
        }
        @media (max-width: 900px) {
            width: 280px;
            height: 26px;
        }
        @media (max-width: 500px) {
            width: 240px;
            height: 22px;
        }
    }
    // .trends-skeleton__price
    &__price {
        width: 207px;
        height: 33px;
        background: #E8E8E8;
        border-radius: 22px;
        @media (max-width: 1245px) {
            width: 180px;
            height: 26px;
        }
        @media (max-width: 900px) {
            width: 168px;
            height: 28px;
        }
        @media (max-width: 500px) {
            width: 144px;
            height: 24px;
        }
    }
}