//AFTER FILTERING NO ITEMS FOUND
.no-items {
    display: flex;
    flex-direction: column;
    padding-bottom: 40px; // .no-items__message
    &__message {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 56px;
        line-height: 70px;
        /* identical to box height */
        text-align: center;
        color: #ffffff;
        @media (max-width: 1350px) {
            font-size: 52px;
            line-height: 60px;
        }
        @media (max-width: 1050px) {
            font-size: 44px;
            line-height: 50px;
            margin-bottom: 0;
        }
        @media (max-width: 830px) {
            font-size: 40px;
            line-height: 43px;
        }
        @media (max-width: 620px) {
            font-size: 35px;
            line-height: 38px;
        }
        @media (max-width: 520px) {
            margin-top: 10px;
            font-size: 32px;
            line-height: 35px;
        }
        @media (max-width: 420px) {
            font-size: 26px;
            line-height: 25px;
        }
    }
    // .no-items__check
    &__check {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 52px;
        line-height: 65px;
        /* identical to box height */
        text-align: center;
        color: #E2E2E2;
        text-decoration: underline 1px solid#fff;
        @media (max-width: 1350px) {
            font-size: 48px;
            line-height: 50px;
        }
        @media (max-width: 1050px) {
            font-size: 42px;
            line-height: 46px;
            margin-bottom: 0;
        }
        @media (max-width: 830px) {
            font-size: 37px;
            line-height: 40px;
        }
        @media (max-width: 620px) {
            font-size: 32px;
            line-height: 34px;
        }
        @media (max-width: 520px) {
            font-size: 28px;
            line-height: 30px;
        }
        @media (max-width: 420px) {
            font-size: 23px;
            line-height: 25px;
        }
    }
}