.categories-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #000000;
    border-radius: 1px;
    transition: all ease 0.3s;
    padding-bottom: 15px;
    &:hover {
        box-shadow: 14px 8px 6px #858585;
    } // .categories-item__img
    &__img {
        width: 345px;
        height: 349px;
        margin-bottom: 15px;
        @media (max-width: 1050px) {
            width: 305px;
            height: 309px;
        }
        @media (max-width: 700px) {
            width: 270px;
            height: 279px;
        }
    }
    // .categories-item__naming
    &__naming {
        text-align: center;
        max-width: 325px;
        display: flex;
        justify-content: center;
        font-family: 'Sedgwick Ave Display';
        font-style: normal;
        font-weight: 400;
        font-size: 29px;
        line-height: 30px;
        /* identical to box height */
        color: #FFFFFF;
        margin-bottom: 15px;
        @media (max-width: 1050px) {
            max-width: 285px;
            font-size: 27px;
            line-height: 28px;
        }
        @media (max-width: 700px) {
            max-width: 275px;
            font-size: 25px;
            line-height: 26px;
        }
    }
    // .categories-item__price
    &__price {
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 32px;
        line-height: 40px;
        /* identical to box height */
        color: #FFFFFF;
        @media (max-width: 1050px) {
            font-size: 29px;
            line-height: 30px;
        }
        @media (max-width: 700px) {
            font-size: 26px;
            line-height: 28px;
        }
    }
}

.categories-load-more {
    cursor: pointer;
    background-color: transparent;
    border: none;
    display: none;
    justify-content: center;
    align-items: center;
    gap: 18px;
    font-family: 'Kenia';
    font-style: normal;
    font-weight: 400;
    font-size: 74px;
    line-height: 74px;
    color: #FFFFFF;
    margin-top: 60px;
    &:hover {
        svg {
            transform: rotate(175deg);
        }
    }
    @media (max-width: 1250px) {
        font-size: 68px;
        line-height: 68px;
    }
    @media (max-width: 700px) {
        font-size: 56px;
        line-height: 56px;
        margin-top: 45px;
        gap: 12px;
    }
    @media (max-width: 470px) {
        font-size: 46px;
        line-height: 50px;
        margin-top: 35px;
        gap: 8px;
    }
    @media (max-width: 360px) {
        font-size: 40px;
        line-height: 45px;
    }
    svg {
        transition: 0.3s all ease;
        @media (max-width: 700px) {
            width: 42px;
            height: 42px;
        }
        @media (max-width: 470px) {
            width: 36px;
            height: 36px;
        }
        @media (max-width: 360px) {
            width: 32px;
            height: 32px;
        }
    }
}