.categories-item {
    // .categories-item__skeleton
    &__skeleton {
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #F1EFEF;
        border-radius: 6px;
        padding-bottom: 10px;
    }
    // .categories-item__skeleton-img
    &__skeleton-img {
        width: 345px;
        height: 349px;
        background: #E8E8E8;
        border-radius: 6px;
        margin-bottom: 25px;
        @media (max-width: 1050px) {
            width: 305px;
            height: 309px;
            margin-bottom: 20px;
        }
        @media (max-width: 700px) {
            width: 270px;
            height: 279px;
            margin-bottom: 12px;
        }
    }
    // .categories-item__skeleton-naming
    &__skeleton-naming {
        width: 345px;
        height: 31px;
        background: #E8E8E8;
        border-radius: 6px;
        margin-bottom: 18px;
        @media (max-width: 1050px) {
            margin-bottom: 15px;
            width: 305px;
            height: 27px;
        }
        @media (max-width: 700px) {
            margin-bottom: 10px;
            width: 270px;
            height: 24px;
        }
    }
    // .categories-item__skeleton-price
    &__skeleton-price {
        width: 207px;
        height: 33px;
        background: #E8E8E8;
        border-radius: 6px;
        @media (max-width: 1050px) {
            width: 187px;
            height: 28px;
        }
        @media (max-width: 700px) {
            width: 157px;
            height: 24px;
        }
    }
}