.categories {
    // .categories__tabs
    &__tabs {
        display: inline-flex;
        flex-wrap: wrap;
        justify-content: center;
        column-gap: 40px;
        row-gap: 25px;
        @media (max-width: 1450px) {
            column-gap: 20px;
            row-gap: 15px;
        }
        @media (max-width: 700px) {
            column-gap: 9px;
            row-gap: 9px;
        }
    }
}

.categories-tabs {
    padding: 0;
    margin: 0;
    & li {
        list-style: none;
    }
    // .categories-tabs__item
    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'Amarante';
        font-style: normal;
        font-weight: 400;
        font-size: 48px;
        line-height: 60px;
        /* identical to box height */
        color: #FFFFFF;
        padding: 8px 40px;
        background: #000000;
        border-radius: 7px;
        @media (max-width: 1450px) {
            padding: 5px 20px;
            font-size: 38px;
            line-height: 40px;
        }
        @media (max-width: 1050px) {
            padding: 3px 7px;
            font-size: 38px;
            line-height: 40px;
        }
        @media (max-width: 900px) {
            font-size: 32px;
            line-height: 37px;
        }
        @media (max-width: 700px) {
            font-size: 28px;
            line-height: 30px;
        }
        @media (max-width: 530px) {
            font-size: 24px;
            line-height: 26px;
        }
        @media (max-width: 400px) {
            font-size: 21px;
            line-height: 23px;
        }
    }
}

.active-tab {
    border: 6px solid #FFFFFF;
    @media (max-width: 1050px) {
        border: 3px solid #FFFFFF;
    }
}